
import ISeeCo from 'assets/images/iseeco.png';
import EHomeSmart from 'assets/images/ehomesmart.png';

interface IEnvironment{
    logo : any;
    name : string;
    connectedColor?: string | undefined
}

type domains = 'app.ehomesmart.net' | 'default' | 'app.iseeco.co'

const environment: {[key in domains]:IEnvironment} = {
    'default': {
        logo: EHomeSmart,
        name: 'eHomeSmart',
        connectedColor: '#8ab13b'
    } ,
    'app.ehomesmart.net': {
        logo: EHomeSmart,
        name: 'eHomeSmart',
        connectedColor: '#8ab13b'
    } ,
    'app.iseeco.co': {
        logo: ISeeCo,
        name: 'ISeeCo',
    }
}

export default function getEnvironment(): IEnvironment{
    return environment[window.location.hostname as domains] || environment['default'];
}